.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px 5px;
  width: 20%;
  height: 100%;
  border-radius: 4px;
  box-shadow: 0px 0px 3px 1px rgba(0, 0, 0, 0.291);
  flex-shrink: 0;

  @media screen and (max-width: 768px) {
    margin-left: 10px;
    width: 22%;
  }

  @media screen and (max-width: 400px) {
    display: none;
  }
}

.title {
  font-weight: 700;
  font-size: 20px;
  margin-bottom: 25px;
}

.list {
  list-style: none;
}

.item {
  & + & {
    margin-top: 15px;
  }
}
